.App {
  background-color: #282c34;
  display: flex;
  flex: 1;
  flex-direction: column;
  align-items: center;
  color: white;
  min-height: 100vh;
}

.App-link {
  color: #61dafb;
}

input {
  padding: 5px 7px;
  font-size: 24px;
  color: #282c34;
  font-family: monospace;
}

.block {
  margin: 9px 0;
}

header {
  margin: 8px 0;
  font-size: 18px;
}

section {
  margin: 5px;
}

.portion {
  height: 38px;
  text-align: center;
  line-height: 38px;
}

.portion.cash {
  background-color: rgb(121, 182, 242);
}

.portion.rsu {
  background-color: rgb(141, 200, 145);
}

.block > div {
  margin-bottom: 3px;
}
.block > div > span {
  font-size: 0.8em;
}

a,
a:visited {
  color: unset;
}
